import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import './App.css';

//https://material-ui.com/components/text-fields/
//https://github.com/facebook/create-react-app

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

function App() {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [values, setValues] = React.useState({
    name: '',
    length: '',
    model: 'Taoist books',
    result: '',
    disabledButton:false
  });

  function handleClickOpen(event) {
    setOpen(true);
  }

  function handleClose() {
    setOpen(false);
  }

  const predict = async (input, length, modelName) => {
    let url;

    if(modelName === "Taoist books"){
      url = "https://tao-gpt2-nga5sjqwkq-uc.a.run.app";
    }else{
      url = "https://tao-book-nga5sjqwkq-uc.a.run.app";
    }


    const fullUrl = url + "/predict?input=" + input + "&length=" + length;
    const response = await fetch(fullUrl);
    const json = await response.json();
    return json.result;
  }

  const inputLabel = React.useRef(null);
  const [labelWidth, setLabelWidth] = React.useState(0);
  React.useEffect(() => {
    //setLabelWidth(inputLabel.current.offsetWidth);
  }, []);
  
  const handleChange = str => event => {
    setValues({ ...values, [str]: event.target.value });
  };

  function handleSelectChange(event) {
    console.log(event.target.name);
    setValues(oldValues => ({
      ...oldValues,
      [event.target.name]: event.target.value,
    }));
  }

  const handleSubmit = async () => {
    setValues({ ...values, ['disabledButton']: true });
    console.log('Your input value is: ' + values.model);
    
    
    const result = await predict(values.name, values.length, values.model);
    console.log(result);
    setValues({ ...values, ['result']: result });
    handleClickOpen();
    

    //setValues({ ...values, ['disabledButton']: false });
  }

  return (
    <div className="App">
      <header className="App-header">
        <form className={classes.container} noValidate autoComplete="off">
          <TextField
            onChange={handleChange('name')}
            id="outlined-full-width"
            label="Predict"
            style={{ margin: 20, width: 250 }}
            className={classes.textField}
            placeholder="What is love?"
            margin="normal"
            variant="outlined"
          />
          <TextField
            onChange={handleChange('length')}
            id="outlined-full-width"
            label="Length"
            style={{ margin: 20, width: 80 }}
            className={classes.textField}
            value="50"
            margin="normal"
            variant="outlined"
          />

          <FormControl className={classes.formControl}>
            <InputLabel htmlFor="model-simple">Model</InputLabel>
            <Select
              value={values.model}
              onChange={handleSelectChange}
              inputProps={{
                name: 'model',
                id: 'model-simple',
              }}
            >
              <MenuItem value="Taoist books">Taoist books</MenuItem>
              <MenuItem value="PJ book">PJ book</MenuItem>
          </Select>
          </FormControl>

          <Button disabled={values.disabledButton}variant="contained" color="primary" onClick={handleSubmit} >
          Predict
          </Button>
        </form>

        <Dialog
          open={open}
          TransitionComponent={Transition}
          keepMounted
          onClose={handleClose}
          aria-labelledby="alert-dialog-slide-title"
          aria-describedby="alert-dialog-slide-description"
        >
          <DialogTitle id="alert-dialog-slide-title">{"Result:"}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-slide-description">
              {values.result}
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose} color="primary">
              Dismiss
            </Button>
          </DialogActions>
        </Dialog>
      </header>
    </div>
  );
}

const useStyles = makeStyles(theme => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
  },
  dense: {
    marginTop: theme.spacing(2),
  },
  menu: {
    width: 200,
  },
  root: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  }
}));

export default App;
